import { Controller } from '@hotwired/stimulus'

// ApexCharts is loaded via importmap - declare types
declare const ApexCharts: {
  new (
    element: HTMLElement,
    options: unknown
  ): {
    render: () => Promise<void>
    destroy: () => void
  }
}

type ApexAxisChartSeries =
  | Array<{
      name: string
      data: number[]
    }>
  | number[]

type ApexChartType = 'line' | 'area' | 'bar' | 'pie' | 'donut'

export default class extends Controller {
  static values = {
    type: String,
    series: Array,
    labels: Array,
    colors: Array,
    height: Number,
  }

  declare typeValue: string
  declare seriesValue: number[]
  declare labelsValue: string[]
  declare colorsValue: string[]
  declare heightValue: number

  private chart: InstanceType<typeof ApexCharts> | null = null
  private renderTimeout?: number

  connect(): void {
    // For pie charts, we need to ensure the element is visible and has dimensions
    if (this.typeValue === 'pie' || this.typeValue === 'donut') {
      // Wait a bit to ensure the element is fully rendered
      this.renderTimeout = window.setTimeout(() => {
        this.renderChart()
      }, 100)
    } else {
      // Use requestAnimationFrame for other charts
      requestAnimationFrame(() => {
        this.renderChart()
      })
    }
  }

  disconnect(): void {
    if (this.chart) {
      this.chart.destroy()
      this.chart = null
    }
    if (this.renderTimeout !== undefined) {
      window.clearTimeout(this.renderTimeout)
    }
  }

  renderChart(): void {
    // Check if we have data to render
    if (!this.seriesValue || this.seriesValue.length === 0) {
      console.log('No series data available')
      this.element.innerHTML =
        '<div class="flex items-center justify-center h-full text-gray-500">No data available</div>'
      return
    }

    console.log('Chart data:', {
      series: this.seriesValue,
      labels: this.labelsValue,
      type: this.typeValue,
    })

    // Limit data points for better performance (max 20 items)
    const maxDataPoints = 20
    const limitedSeries = this.seriesValue.slice(0, maxDataPoints)
    const limitedLabels = this.labelsValue.slice(0, maxDataPoints)

    let seriesData: ApexAxisChartSeries | number[]

    // Format series data based on chart type
    if (this.typeValue === 'pie' || this.typeValue === 'donut') {
      seriesData = limitedSeries
    } else if (this.typeValue === 'bar') {
      seriesData = [
        {
          name: 'Amount',
          data: limitedSeries,
        },
      ]
    } else if (this.typeValue === 'line') {
      seriesData = [
        {
          name: 'Amount',
          data: limitedSeries,
        },
      ]
    } else {
      seriesData = limitedSeries
    }

    interface ChartOptions {
      chart: {
        type: ApexChartType
        height: number
        width?: number
        fontFamily: string
        toolbar: { show: boolean }
        animations?: { enabled: boolean; easing: string; speed: number }
        redrawOnParentResize: boolean
        redrawOnWindowResize: boolean
      }
      series: ApexAxisChartSeries
      labels: string[]
      colors: string[]
      dataLabels: {
        enabled: boolean
        style: { fontSize: string; fontWeight: string }
        formatter: (val: number, opts?: unknown) => string
      }
      legend: { show: boolean }
      tooltip: {
        enabled: boolean
        y: { formatter: (val: number) => string }
      }
      noData: {
        text: string
        align: string
        verticalAlign: string
        style: { color: string; fontSize: string }
      }
      plotOptions?: {
        pie?: { donut?: { size: string }; expandOnClick: boolean }
        bar?: {
          horizontal: boolean
          borderRadius: number
          dataLabels: { position: string }
        }
      }
      xaxis?: { categories: string[]; type: string }
      yaxis?: { title: { text: string } }
      stroke?: { curve: string; width: number }
      markers?: { size: number; hover: { size: number } }
      [key: string]: unknown
    }

    const options: ChartOptions = {
      chart: {
        type: this.typeValue as ApexChartType,
        height: this.heightValue || 400,
        fontFamily: 'Inter, sans-serif',
        toolbar: {
          show: false,
        },
        // Performance optimizations
        animations: {
          enabled: true, // Enable animations for pie charts
          easing: 'easeinout',
          speed: 300,
        },
        redrawOnParentResize: true,
        redrawOnWindowResize: true,
      },
      series: seriesData,
      labels: limitedLabels,
      colors: this.colorsValue || [
        '#3B82F6',
        '#EF4444',
        '#10B981',
        '#F59E0B',
        '#8B5CF6',
        '#06B6D4',
        '#84CC16',
        '#F97316',
        '#EC4899',
        '#6B7280',
      ],
      dataLabels: {
        enabled: true,
        style: {
          fontSize: '12px',
          fontWeight: 'bold',
        },
        formatter: (val: number, opts?: unknown): string => {
          if (this.typeValue === 'pie' && opts) {
            const config = (opts as { w?: { config?: ChartOptions } }).w?.config
            const labelIndex = (opts as { seriesIndex?: number }).seriesIndex ?? 0
            const labels = config?.labels
            const label = labels?.[labelIndex] || ''
            return `${label}: ${val.toFixed(1)}%`
          }
          return val.toFixed(0)
        },
      },
      legend: {
        show: false,
      },
      tooltip: {
        enabled: true,
        y: {
          formatter: (val: number): string => {
            return '$' + val.toFixed(0)
          },
        },
      },
      // Performance optimization for large datasets
      noData: {
        text: 'No data available',
        align: 'center',
        verticalAlign: 'middle',
        style: {
          color: '#6B7280',
          fontSize: '14px',
        },
      },
    }

    // Add specific options based on chart type
    if (this.typeValue === 'pie' || this.typeValue === 'donut') {
      options.plotOptions = {
        pie: {
          donut: {
            size: this.typeValue === 'donut' ? '70%' : '0%',
          },
          expandOnClick: false,
        },
      }

      // Set explicit dimensions for pie chart container
      const element = this.element as HTMLElement
      const containerWidth = element.offsetWidth || 400
      const containerHeight = this.heightValue || 400

      options.chart.width = containerWidth
      options.chart.height = containerHeight

      // Ensure the element itself has dimensions
      if (!element.style.height) {
        element.style.minHeight = containerHeight + 'px'
      }

      // Force pie chart to render
      options.chart.animations = {
        enabled: true,
        easing: 'easeinout',
        speed: 400,
      }
    }

    if (this.typeValue === 'bar') {
      options.plotOptions = {
        bar: {
          horizontal: true,
          borderRadius: 4,
          dataLabels: {
            position: 'center',
          },
        },
      }
      options.xaxis = {
        categories: limitedLabels,
        type: 'category', // Use category type for better performance
      }
    }

    if (this.typeValue === 'line') {
      options.stroke = {
        curve: 'smooth',
        width: 3,
      }
      options.xaxis = {
        categories: limitedLabels,
        type: 'category', // Use category type for better performance
      }
      options.yaxis = {
        title: {
          text: 'Amount ($)',
        },
      }
      // Performance optimization for line charts
      options.markers = {
        size: 4,
        hover: {
          size: 6,
        },
      }
    }

    // Create chart with error handling
    try {
      const element = this.element as HTMLElement
      console.log(`Rendering ${this.typeValue} chart with:`, {
        series: seriesData,
        seriesType: typeof seriesData,
        seriesIsArray: Array.isArray(seriesData),
        labels: limitedLabels,
        colors: this.colorsValue,
        element: element,
        elementDimensions: {
          width: element.offsetWidth,
          height: element.offsetHeight,
          clientWidth: element.clientWidth,
          clientHeight: element.clientHeight,
        },
        options: options,
      })

      this.chart = new ApexCharts(element, options)
      this.chart
        .render()
        .then(() => {
          console.log(`${this.typeValue} chart rendered successfully!`)
        })
        .catch((error: Error) => {
          console.error('Chart rendering failed:', error)
          this.element.innerHTML =
            '<div class="flex items-center justify-center h-full text-red-500">Chart rendering failed</div>'
        })
    } catch (error) {
      console.error('Chart creation failed:', error)
      this.element.innerHTML =
        '<div class="flex items-center justify-center h-full text-red-500">Chart creation failed</div>'
    }
  }
}
